<template>
  <div class="app flex-row align-items-center">
    
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            
            <b-card no-body class="p-4">
              <div v-if="alert.message" :class="`alert ${alert.type}`">{{alert.message}}</div>
              <b-card-body>
                <b-form @submit.prevent="handleSubmit">
                  <h1>Login</h1>
                  <p class="text-muted">Sign In to your account</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend><b-input-group-text><i class="icon-user"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="text" v-model="username" name="username" class="form-control" :class="{ 'is-invalid': submitted && !username }"
                    placeholder="Username" autocomplete="username" />
                    <div v-show="submitted && !username" class="invalid-feedback">Username is required</div>
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend><b-input-group-text><i class="icon-lock"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input :class="{ 'is-invalid': submitted && !password }" type="password" v-model="password" name="password" class="form-control" placeholder="Password" autocomplete="current-password" />
                     <div v-show="submitted && !password" class="invalid-feedback">Password is required</div>
                  </b-input-group>
                  <b-row>
                    <b-col cols="6">
                      <b-button variant="primary" type="submit" :disabled="status.loggingIn" class="px-4">Login</b-button>
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <b-button variant="link" class="px-0">Forgot password?</b-button>
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
            <b-card no-body class="text-white bg-primary py-5 d-md-down-none" style="width:44%">
              <b-card-body class="text-center">
                <div>
                  <h2>Sign up</h2>
                  <p>Register using reference number</p>
                  <!-- <b-button href="#/register" variant="primary" class="active mt-3">Register Now!</b-button> -->
                </div>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<!-- script>
